.cs-slide-div {
  width: 100%;
  height: 100%;
  position: absolute;
}
.cs-slide-div img {
  width: 100%;
  height: 100%;
}
.cs-main-slide {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}
.cs-slide-round {
  position: absolute;
  left: 0px;
  right: 0px;
  bottom: 0px;
  margin: auto;
  width: 150px;
  height: 20px;
  text-align: center;
}
.cs-slide-round i::after {
  content: '';
  background-color: #ebedf0;
  width: 8px;
  height: 8px;
  border-radius: 5px;
  display: inline-block;
  opacity: 0.3;
  -webkit-transition: opacity 0.2s;
  transition: opacity 0.2s;
}
.cs-slide-round i:not(:last-child) {
  margin-right: 5px;
}
.cs-slide-round .i-active::after {
  background-color: #fff;
  opacity: 1;
}
